  .wrapper--slide {
    width: 1230px;
    padding-top: 30px;
    padding-bottom: 84px;
    grid-template-areas: "image badge badge" "image title title" "image description description" "image price link";
    grid-template-columns: 1fr 230px 1fr;
    grid-template-rows: auto minmax(80px, min-content) minmax(100px, min-content) max-content;
    justify-items: start;
    text-align: start;
    column-gap: 30px;
  }
}
