    padding-bottom: 120px;
    display: grid;
    grid-template-areas: "label label" "title title" "filter sorting" "filter catalog" "filter pagination";
    grid-template-columns: 318px 1fr;
    column-gap: 30px;
  }
}
.wrapper--footer {
  margin: 0;
}
