.main-nav {
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  z-index: 2;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav {
    display: inline-block;
    position: static;
    width: auto;
  }
}

.main-nav__toggle {
  display: block;
  width: 60px;
  height: 60px;
  position: absolute;
  top: -60px;
  right: 0;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
}
.main-nav__toggle:hover, .main-nav__toggle:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 24px;
  width: 17px;
  height: 2px;
  background-color: #333333;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 23px;
  left: 22px;
  width: 16px;
  height: 2px;
  background-color: #333333;
  box-shadow: 0 6px 0 0 #333333, 0 12px 0 0 #333333;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs {
    position: static;
    display: block;
    order: 2;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__list {
    position: static;
    display: block;
  }
}

