.control {
  position: relative;
  display: block;
  padding-left: 34px;
}
.control--checkbox {
  padding-left: 54px;
}

.control__label {
  font-size: 16px;
  line-height: 1.2;
  cursor: pointer;
}

.control__mark-radio {
  width: 24px;
  height: 24px;
  box-sizing: border-box;
  position: absolute;
  top: -2px;
  left: 0;
  border: 2px solid #bdbdbd;
  border-radius: 50%;
  cursor: pointer;
}
@media (min-width: 768px) {
  .control__mark-radio {
    top: 2px;
  }
}

.control:hover .control__mark-radio,
.control:focus-within .control__mark-radio {
  border-color: #9070ec;
}
.control:hover .control__mark-radio::before,
.control:focus-within .control__mark-radio::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: rgba(120, 89, 207, 0.25);
  border-radius: 50%;
  top: 2px;
  left: 2px;
}

.control:active .control__mark-radio {
  border-color: rgba(120, 89, 207, 0.25);
}

.control__input:checked + .control__mark-radio {
  border-color: #9070ec;
}
.control__input:checked + .control__mark-radio::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: #9070ec;
  border-radius: 50%;
  top: 2px;
  left: 2px;
}

.control__input:disabled ~ .control__label {
  color: #bdbdbd;
}

.control__input:disabled + .control__mark-radio {
  border-color: #e2e2e2;
}
.control__input:disabled + .control__mark-radio::before {
  display: none;
}

.control__input:checked:disabled + .control__mark-radio::before {
  background-color: #e2e2e2;
}

.control__mark-checkbox {
  width: 42px;
  height: 24px;
  box-sizing: border-box;
  position: absolute;
  top: -4px;
  left: 2px;
  background-color: #bdbdbd;
  border: 2px solid #bdbdbd;
  border-radius: 12px;
  cursor: pointer;
}
.control__mark-checkbox::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
  border-radius: 50%;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .control__mark-checkbox {
    top: 3px;
  }
}

.control--checkbox:hover .control__mark-checkbox,
.control--checkbox:focus-within .control__mark-checkbox {
  border-color: #9070ec;
  background-color: rgba(120, 89, 207, 0.25);
}

.control--checkbox:active .control__mark-checkbox {
  border-color: rgba(120, 89, 207, 0.25);
  background-color: rgba(120, 89, 207, 0.25);
}

.control__input:checked + .control__mark-checkbox {
  border-color: #7859cf;
  background-color: #7859cf;
}
.control__input:checked + .control__mark-checkbox::before {
  left: auto;
  right: 0;
}

.control__input:disabled + .control__mark-checkbox {
  border-color: #e2e2e2;
  background-color: #e2e2e2;
}

.control__input:checked:disabled + .control__mark-checkbox::before {
  background-color: #bdbdbd;
  left: auto;
  right: 0;
}

